#body {
    background-image: url('https://images.unsplash.com/photo-1620202119243-212a883c5627?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=967&q=80');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    /* Center the image */
}

#div {
    background-color: rgba(255, 255, 255, 0.5);
    padding-top: 60px;
    padding: 30px;
    margin-left: 60px;
    margin-right: 60px;
    border-radius: 40px;
}

#divForm {
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 20px;
    margin: 20px;
    height: 200px;
}

form {
    float: right;
    padding-top: 40px;
}

#add {
    float: left;
    padding-top: 80px;
    padding-left: 50px
}

#tableRow {
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 20px;
}

td {
    border: solid 1px #000;
    border-style: solid none;
    padding: 10px;
}

td:first-child {
    border-left-style: solid;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

td:last-child {
    border-right-style: solid;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
}

.table {
    border-collapse: separate;
    border-spacing: 0 15px;
}

tr.spaceUnder>td {
    padding-bottom: 1em;
}